


class TodoItem {

    constructor(title, description, dueDate, priority, notes) {
        this.title = title
        this.description = description
        this.dueDate = dueDate
        this.priority = priority
        this.notes = notes

    }
};

function addItemToProject() {
    const item = new TodoItem(
        prompt("title?"),
        prompt("description?"),
    );
    console.log(item);
}

addItemToProject();